.locations-list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    width: 80%;
    margin: 0 auto;
    gap: 20px;
}

.location-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 15px;
    overflow: hidden;
    transition: transform 0.3s ease;
    padding: 10px;
    cursor: pointer;
    
}

.location-item:hover {
    transform: scale(1.05); 
}

.location-item img {
    width: 200px;
    height: 150px;
    object-fit: cover;
    border-radius: 10px;
    transition: transform 0.3s ease;
}

.location-item img:hover {
    transform: scale(1.05);
}

.location-item h3 {
    text-align: center;
    font-size: 18px;
    font-weight: bold;
    padding: 10px;
    color: var(--color-black);
}

@media (max-width: 1000px) {
    .locations-list {
        grid-template-columns: 1fr; 
        width: 100%;
        padding: 10px;
    }
}
